body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.app {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 6rem;
  color: rgba(255, 255, 255, 0.87);
  text-rendering: optimizeLegibility;
}

h1 {
  font-weight: 600;
  margin: 16px;
}

a {
  color: rgba(33, 150, 243, 0.87);
  font-size: 2rem;
}
